video { 
       object-fit: cover;
       width: 100%;
       height: 100%;
       position: fixed;
       z-index: -1;
     }
     
     .hero-container {
       /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
       background: linear-gradient(108.21deg, #2B3A67 0%, #0D03C3 97.89%);
       height: 100vh;
       width: 100%;
       display: flex;
       flex-direction: column;
       justify-content: center;
       align-items: center;
       box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
       object-fit: contain;
     }
     
     .hero-container > h1 {
       color: #fff;
       font-size: 60px;
       margin-top: -100px;
       text-align: center;
       font-family: 'Manrope';
       font-style: normal;
     }
     
     .hero-container > p {
       margin-top: 8px;
       color: #fff;
       font-size: 30px;
       font-family: 'Manrope';
       font-style: normal;
       font-weight: 400;
         text-align: center;
     }
     
     .hero-btns {
       margin-top: 32px;
     }
     
     .hero-btns .btn {
       margin: 6px;
     }
     
     .fa-play-circle {
       margin-left: 4px;
     }
     
     @media screen and (max-width: 960px) {
       .hero-container > h1 {
         font-size: 40px;
         margin-top: -150px;
         text-align: center;
       }
     }
     
     @media screen and (max-width: 768px) {
       .hero-container > h1 {
         font-size: 30px;
         margin-top: -100px;
         text-align: center;
       }
     
       .hero-container > p {
         font-size: 20px;
       }
     
       .btn-mobile {
         display: block;
         text-decoration: none;
       }
     
       .btn {
         width: 100%;
       }
     }


     @media screen and (max-width: 425px) {
      .hero-container > h1 {
        font-size: 30px;
        margin-top: -100px;
        text-align: center;
      }
    
      .hero-container > p {
        font-size: 20px;
        text-align: center;
      }
    
      .btn-mobile {
        display: block;
        text-decoration: none;
      }
    
      .btn {
        width: 100%;
      }
    }