@media only screen and (min-width: 1024px) {
       .cards__item {
              margin-bottom: 2rem;
       }
}

.my-img-container{
       display: flex;
       align-items: center;
       margin-bottom: 20rem;
       margin-top: 10rem;
      
}
.my-img {
       position: absolute;
       right: 0;
       left: 0;
       bottom: 50;
       display: block;
       width: 100%;
       max-width: 100%;
       height: 100%;
       max-height: 100%;
       object-fit: contain;
       transition: all 0.2s linear;
}
.header-card {

       margin-top: 15rem;
       margin-bottom: -10rem;
       padding: 2rem;
}